.App {
  font-family: sans-serif;
  text-align: center;
  margin: 2%;
  /* background: lightgray; */
  font-family: auto;
  height: 100vh;
}
h2 {
  color: blueviolet;
}
.heading {
  font-size: 20px;
  color: red;
  display: flex;
  font-weight: bold;
  background: lightyellow;
}

.heading img {
  width: 60px;
}
.headingText {
  width: 90%;
  font-size: 23px;
  line-height: 44px;
}
.form {
  width: 100%;
}

.container {
  padding: 20px;
}
.manPage {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.manPage div {
  margin: 10px;
}

.mainPageContainer {
  /* display: flex;
  flex-direction: column;

  align-items: center; */
}

.alert {
  background: lightgreen;
  height: 40px;
  width: 100%;
  font-weight: bolder;
}
.formWrapper {
  padding: 0 20%;
}

.imageContainer {
  width: 80%;
  height: 130px;
  display: flex;
  /* justify-content: center; */
  /* border: 2px solid green; */
  /* align-content: center; */
}
.imageBox {
  border: 1px solid grey;
  width: 20%;
}
.paginationButton1 {
  height: 130px;
  /* margin: 30%; */
  align-self: center;
  border: grey;
  color: blue;
  border-radius: 20%;
  font-weight: bold;
  float: left;
  font-size: 30px;
  width: 5%;
  position: relative;
}
.paginationButton2 {
  height: 130px;

  /* margin: 30%; */
  align-self: center;
  border: grey;
  color: blue;
  font-weight: bold;
  float: right;
  font-size: 30px;
  width: 5%;
  position: relative;
}
.footer {
  width: 100%;
  height: 50px;
  background: lightseagreen;
}
